#body {
  width: 100%;
  height: 100vh;
  background: rgb(23, 255, 23, 0.3);
  overflow: hidden;
}
.btn {
  border-radius: 10px;
  font-size: 20px;
  margin: 10px;
  width: 100px;
}
/* result Area */
.resultSpan {
  background-color: rgb(0, 60, 145, 0.4);
  border-radius: 10px;
  padding: 15px 20px 10px 20px;
  margin: 0 0 105px -5px;
  color: white;
  font-size: 30px;
  width: 101%;
  height: 8vh;
}
/* Calculator */
#main {
  width: 100%;
  margin-left: 25%;
  margin-top: 5%;
}
/* Calculator background */
.wrapper {
  border-radius: 10px;
  background: rgb(0, 0, 255, 0.4);
  width: 50%;
  padding: 50px;
}
.result {
  margin-left: 190px;
}
.divider,
.multiplier,
.adder,
.minus {
  margin-right: -100px;
}
